/* purgecss start ignore */

.lc-chart-tooltip {
  --tw-bg-opacity: 1;
  background-color: rgba(var(--lc-color-white), var(--tw-bg-opacity));
  font-size: 14px;
  line-height: 1.5;
  padding: 8px;
  border-radius: 8px;
  border-width: 1px;
  --tw-border-opacity: 1;
  border-color: rgba(var(--lc-color-gray-lighter), var(--tw-border-opacity));
  opacity: 0;
  transition: opacity 1s ease-in;
  display: flex;
  align-items: center;
  position: absolute;
  pointer-events: none;
  text-align: center;
  transform: translate(-50%, -100%);
  transition: all 0.3s ease-in-out;
  box-shadow: 0px 12px 20px rgba(0, 0, 0, 0.1);
}

.lc-chart-tooltip-color {
    display: inline-block;
    border-radius: 100%;
    height: 11px;
    width: 11px;
    margin-right: 8px;
  }

.lc-chart-tooltip.loaded {
    opacity: 1;
  }

/* purgecss end ignore */
